import { useState, useEffect } from 'react';
import Validation from './Validation.js';
import axios from 'axios';



function SignUpForm({submitForm}) {
    // const [inputValues, setValues] = useState({
    //     email: '',
    //   });
    
      const [inputValues, setValues] = useState('');
    
    const [errors, setErrors] = useState({});
    const [dataIsCorrect, setDataIsCorrect] = useState(false);

    // const handleChange = (event) => {
    //     //same as writing setValues({username: e.target.value})
    //     setValues({
    //       ...inputValues,
    //       [event.target.name]: event.target.value,
    //     })
    //   };
    
      const addEmail = () => {
        axios.post('https://s0nshulo19.execute-api.us-east-1.amazonaws.com/default/code-challenge',{
            email: inputValues,
          }).then(response => {
              console.log("print",response.data)
          })
          //set the input filed to empty string after submit the form
          setValues('')
    }

    const handleFormSubmit = (event) => {
        event.preventDefault();
        setErrors(Validation(inputValues))
        setDataIsCorrect(true);
        addEmail();
        
      };
    
    useEffect(() => {
        if (Object.keys(errors).length === 0 && dataIsCorrect) {
          submitForm(true);
        }
      }, [errors])

    // const signUp = async (userData) => {
    //     const response = await fetch('https://s0nshulo19.execute-api.us-east-1.amazonaws.com/default/code-challenge', {
    //      method: 'POST',
    //      headers: {'Content-Type': 'application/json'},
    //      body: JSON.stringify(userData)})
    //      const data = await response.json()
    //      console.log(data)
    //     //  setUserData('')
    //     }

    return (
      <div>   
        <form>  
                <input id="m" placeholder="   Email" type='email' name='email' value={inputValues.email} onChange={e => setValues(e.target.value)} />
                <button onClick={handleFormSubmit}>SUBMIT</button>       
        </form>
                {errors.email && <p className='error-message'>{errors.email}</p>}
      </div>  
    )
}

export default SignUpForm
